import { Button, Typography, Box } from '@material-ui/core';
import Container from '@material-ui/core/Container';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import ListCard from './ListCard';
import { Link } from 'react-router-dom';

const useStyles = makeStyles({
  CardArea: {
   display: 'grid',
   gridTemplateColumns: 'repeat(3, 1fr)',
   gridAutoRows: '18rem',
   justifyItems: 'center',
   alignItems: 'stretch',
   gap: '1rem',
   padding: '1rem'
  },

  ButtonArea: {
    marginLeft: 20
  }
});

const WhiteText = withStyles({
  root: {
    color: "#FFFFFF"
  }
})(Typography);


export default function MyLists(props) {
  const classes = useStyles();

  const deleteCard = (id) => {
    const values = [...props.data];
    values.splice(values.findIndex((obj) => {
      return obj.id === id;
    }), 1);
    props.setData(values)
    localStorage.setItem("data", JSON.stringify([...values]));
  }

  return (
    <Container maxWidth="md"  style={{ padding: '0', height: '100%', backgroundColor: '#FFFFFF'}}>
      <Box style={{padding: '3rem 2rem', background: "linear-gradient(45deg, #ec4d90, transparent), linear-gradient(45deg, #fff, transparent)"}}>
        <WhiteText variant="h4" style={{ paddingTop: 20 }}>My lists</WhiteText>
        <WhiteText variant="h5" style={{ marginTop: 10 }}>View your lists </WhiteText>
        <WhiteText variant="h5" style={{marginBottom: 50}}>You can delete lists by clicking the delete icon</WhiteText>
      </Box>
      <Box className={classes.CardArea}>
      {props.data.length !== 0
        ? [
          props.data.map((list, index) => {
            return(
              <ListCard 
                key={index}
                id={list.id}
                name={list.name}
                children={list.children}
                deleteCard={deleteCard}
              />  
            )
          })
        ]
        : [
          <>
            <WhiteText variant="h5" style={{marginTop: 80, marginBottom: 80}}>No lists yet, start by creating a new list</WhiteText>
            <Button 
              className={classes.ButtonArea}
              component={Link}
              to="/shoppinglist/newlist"
            >
              Create new list
            </Button>
          </>
        ]
      }
      </Box>
    </Container>
  );
}