import Container from '@material-ui/core/Container';
import { withStyles } from '@material-ui/core/styles';
import { Box, Typography } from '@material-ui/core';

const WhiteText = withStyles({
  root: {
    color: "#FFFFFF"
  }
})(Typography);

export default function Home() {
 
  return (
    <Container maxWidth="md" style={{ padding: '0', height: '100%', backgroundColor: '#FFFFFF'}}>
      <Box style={{padding: '3rem 2rem', background: "linear-gradient(45deg, #ec4d90, transparent), linear-gradient(45deg, #fff, transparent)"}}>
        <WhiteText variant="h4">Shoppinglist</WhiteText>
        <WhiteText variant="h5" style={{ paddingTop: 10, width: '60%' }}>Create your own shoppinglist and you wont forget that special something.</WhiteText>
      </Box>
    </Container>
  );
}