import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Button from '@material-ui/core/Button';
import { CssBaseline } from '@material-ui/core';
import { Link } from 'react-router-dom';
import Container from '@material-ui/core/Container';
import { createMuiTheme, makeStyles, ThemeProvider } from '@material-ui/core/styles';
import HomeTwoToneIcon from '@material-ui/icons/HomeTwoTone';
import CreateTwoToneIcon from '@material-ui/icons/CreateTwoTone';
import ListTwoToneIcon from '@material-ui/icons/ListTwoTone';


const theme = createMuiTheme({
  overrides: {
    MuiButton: {
      label: {
        color: "#3A3535",
      },
    },
  },
});

const useStyles = makeStyles(() => ({
  appbar: {
    alignItems: 'left',
  },
  button: {
    margin: '0 0.75rem'
  }
}));

export default function Appbar() {
  const classes = useStyles();

  return (
    <CssBaseline>
      <Container maxWidth="md" style={{ padding: '0', background: '#F2BCBC' }}>
      <ThemeProvider theme={theme}>
        <AppBar style={{ background: '#FFFFFF' }} className={classes.appbar} position="static">
          <Toolbar>
            <Button
              className={classes.button}
              startIcon={<HomeTwoToneIcon>home</HomeTwoToneIcon>}
              component={Link}
              to={"/shoppinglist/"}
            >
              Home
            </Button>
            <Button
              className={classes.button}
              startIcon={<CreateTwoToneIcon>new shopping list</CreateTwoToneIcon>}
              component={Link}
              to={"/shoppinglist/newlist"}
            >
              New shopping list
            </Button>
            <Button
              className={classes.button}
              startIcon={<ListTwoToneIcon>my lists</ListTwoToneIcon>}
              component={Link}
              to={"/shoppinglist/mylists"}
            >
              My lists
            </Button>
          </Toolbar>
        </AppBar>
        </ThemeProvider>
      </Container>
    </CssBaseline>

  );
}