import React from 'react';
import { Typography, TextField, IconButton, Button, Box } from '@material-ui/core';
import Container from '@material-ui/core/Container';
import AddIcon from '@material-ui/icons/Add';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import { useState } from 'react';
import { v4 as uuid} from 'uuid';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import DeleteIcon from '@material-ui/icons/Delete';
import { useHistory } from 'react-router-dom';

const useStyles = makeStyles({
  mainInput: {
    width: '60%',
    marginBottom: 30,
    backgroundColor: '#FFF',
    color: 'black',
    borderRadius: '12px',

    '& label': {
      color: '#656565',
    },
  },

  form: {
    display: 'flex',
    flexFlow: 'column nowrap',
    height: '100%'
  },

  formItemArea: {
    flexGrow: 1,
    padding: '1rem 2rem',
  },

  contentArea: {
    flexGrow: 1,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between'
  },

  inputfieldsArea: {
    dislay: 'flex',
    flexDirection: 'column',
  },

  inputRow: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'flex-start',
    alignItems: 'center',
    marginBottom: '15px'
  },

  btnGroup: {
    margin: ' 0 2rem'
  },

  formControlsArea: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'flex-end',
    padding: '0 3rem'
  },
});

const CssTextField = withStyles({
  root: {
    '& label.Mui-focused': {
      color: '#555',
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: '#ec4d90',
    },
    '& .MuiOutlinedInput-root': {
      '& fieldset': {
        borderColor: '#ec4d90',
        borderRadius: '12px',
      },
      '&.Mui-focused fieldset': {
        borderColor: '#ec4d90  ',
      },
    },
  },
})(TextField);

function NewList(props) {
  let history = useHistory();

  const [name, setName] = useState("");
  const [inputfields, setInputfields] = useState([{ id: uuid(), value: '', quantity: 0 }]);
  const classes = useStyles();
  
  const handleFormSubmit = (e) => {
    e.preventDefault();
    const newData = {
      id: uuid(),
      name: name,
      children: inputfields
    }
    props.setData([...props.data, newData]);
    localStorage.setItem("data", JSON.stringify([...props.data, newData]));
    history.push("/mylists");
  }

  const handleChangeInput = (event, idx) => {
    const values = [...inputfields];
    values[idx][event.target.name] = event.target.value;
    setInputfields(values)
  }

  const handleChangeQuantity = (idx, type) => {
    const values = [...inputfields];

    switch (type) {
      case 'add':
        values[idx].quantity += 1;
        break;
      case 'sub':
        if(values[idx].quantity > 0) {
          values[idx].quantity -= 1;
        }
        break;
    }
    setInputfields(values)
  }

  const handleAddField = () => {
    setInputfields([...inputfields, { id: uuid(), value: "", quantity: 0 }])
  }

  const removeItem = (id) => {
    const values = [...inputfields];
    values.splice(values.findIndex((obj) => {
      return obj.id === id;
    }), 1);
    setInputfields(values)
  },

  handleCancel = () => {
    history.goBack()
  }

return (
    <Container maxWidth="md" style={{ padding: '0', height: '100%', backgroundColor: '#FFFFFF' }}>
      <form className={classes.form} onSubmit={handleFormSubmit}>
        <Box style={{padding: '1rem 2rem', background: "linear-gradient(45deg, #ec4d90, transparent), linear-gradient(45deg, #fff, transparent)"}}>
          <Typography variant="h4" style={{ color: "#fff", marginTop: 20, marginBottom: 10, fontWeight: 'bold' }}>
            Create my new shopping list
          </Typography>
          <Typography variant="h6" style={{ color: "#fff", marginTop: 40, }}>
            Name of my list
          </Typography>
          <CssTextField
            className={classes.mainInput}
            variant="outlined"
            value={name} 
            onChange={e => setName(e.target.value)}
          />
        </Box>
        <Box className={classes.contentArea}>
          <Box className={classes.formItemArea}>
          <Typography variant="h5" style={{ color: "#3A3535", marginTop: 20, marginBottom: 20 }}>
            My list items
          </Typography>
          <Box className={classes.inputfieldsArea}>
            {inputfields.map((field, idx) => {
              return(
                <Box key={idx} className={classes.inputRow}>
                  <CssTextField
                    style={{ width: '100%' }}
                    label="Item"
                    variant="outlined"
                    name="value"
                    value={field.value}
                    onChange={event => handleChangeInput(event, idx)}
                  />
                  <ButtonGroup className={classes.btnGroup}>
                    <Button
                      name="quantity"
                      onClick={() => handleChangeQuantity(idx, 'sub')}
                    >
                      -
                    </Button>
                    <Button disabled>{field.quantity}</Button>
                    <Button
                      name="quantity"
                      onClick={() => handleChangeQuantity(idx, 'add')}
                    >
                      +
                    </Button>
                  </ButtonGroup>
                  <IconButton onClick={() => removeItem(field.id)}>
                    <DeleteIcon/>
                  </IconButton>
                </Box>
              )
            })}
            <IconButton onClick={() => handleAddField()}>
              <AddIcon />
            </IconButton>
          </Box>
        </Box>
        <Box className={classes.formControlsArea}>
          <Button 
            onClick={() => handleCancel()}
            variant="contained"
            style={{ marginTop: 20, marginRight: 20, marginBottom: 20, marginLeft: 20, backgroundColor: '#F2F2F2' }}
          >
            Cancel
          </Button>
          <Button 
            type="submit"
            variant="contained"
            style={{ marginTop: 20, marginBottom: 20, backgroundColor: '#ec4d90' }}
          >
            Save
          </Button>
        </Box>
        </Box>
      </form>
    </Container>
);
}

export default NewList;
